
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Checker from 'vux/src/components/checker/checker.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import { GetSpecialty, GetUserInfo } from 'src/Api/api'
import {
mapActions
} from 'vuex'
import {
USER_SIGNOUT
} from 'src/store/user'

export default {
directives: {
TransferDom
},
components: {
XButton,
Group,
Cell,
Checker,
CheckerItem,
XTextarea
},
data () {
return {
user: {
NickName: '',
Name: '',
Sex: '',
Tel: '',
Company: '',
Room: '',
Position: '',
Email: '',
Weibo: '',
area: '',
Description: '',
Specialties: []
},
Specialties: [],
spe: [],
area: []
}
},
created () {
GetSpecialty().then(response => {
console.log('GetSpecialty', response.data)
this.Specialties = response.data
})
this.GetList()
},
methods: {
...mapActions([USER_SIGNOUT]),
GetList () {
GetUserInfo({openid: this.$store.state.user.token}).then(response => {
var vm = this
console.log('GetUserInfo', response.data)
this.user = response.data.data.wxUser
if (this.user.area) {
this.area = [this.user.area.replace(/(\d\d)\d\d\d\d/ig, '$1' + '0000'), this.user.area.replace(/(\d\d\d\d)\d\d/ig, '$1' + '00'), this.user.area]
}
var xx = this.user.specialties
xx.forEach(
function (value, index) {
vm.spe.push({specialtyId: value.specialtyId, name: value.specialty.name})
})
})
},
GoUrl () {
this.$router.push('/edituserinfo')
},
LogOut () {
this.USER_SIGNOUT()
},
getName (value) {
return value2name(value, ChinaAddressV4Data)
}
}
}



